import {
    AnimationBaseController,
    GameRandom,
    IAnimationControllerConfiguration,
    Inject,
} from "@denishussar/html5-game-engine";
import {LeverModel} from "../models/GameModel";
import {GameLogic} from "../logic/GameLogic";

export interface ILeverControllerOptions {
}

export class LeverController extends AnimationBaseController {
    $model: LeverModel;

    public constructor(@Inject private options: ILeverControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("LeverController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            modelWatch: {
                state: () => this.updateState(),
            },
        };
    }

    protected onInitialize(): void {
        this.updateState();
    }

    private updateState() {
        this.gotoAndStop(this.$model.state);
        this.x = this.$model.position.x;
        this.y = this.$model.position.y;
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
    }
}

