import {GameRandom, Inject, ModelEvents, Singleton} from "@denishussar/html5-game-engine";
import {PlayerModel} from "./PlayerModel";
import {PhysicsModel} from "./PhysicsModel";
import {GameConstants} from "../constants/GameConstants";
import {Body, Vector} from "matter-js";
import {PhysicsConstant} from "../constants/PhysicsConstant";
import goldHealth = GameConstants.goldHealth;
import timer = GameConstants.timer;
import startPlayerPosition = PhysicsConstant.startPlayerPosition;


@Singleton
export class GameModel {

    public wasdState = new Map;

    public physic = new PhysicsModel();

    public levers = [new LeverModel(), new LeverModel()];
    public canToggleLever = false;

    public goldHealth = goldHealth;
    public goldStage = 4;
    public playerScore = 0;

    public isStart = false;

    //TODO: REWORK DUPLICATED SETTERS!@!!!!
    get fireballCooldown() {
        return this.player.fireballCooldown;
    }

    set fireballCooldown(value) {
        this.player.fireballCooldown = value;
    }

    get dashCooldown() {
        return this.player.dashCooldown;
    }

    set dashCooldown(value) {
        this.player.dashCooldown = value;
    }

    public isManholeOpen = false;
    public isGoldDropped = false;

    public timer = timer;
    public currentMusic: "intro" | "action" | "rush";

    constructor(@Inject private random: GameRandom,
                @Inject private modelEvents: ModelEvents,
                @Inject public player: PlayerModel,
    ) {
        this.player.collision = this.physic.playerCollision;
        this.createLevers();
    }

    public reset() {
        this.wasdState.clear();
        this.physic.reset();
        this.player.reset();
        Body.setPosition(this.physic.playerCollision, Vector.create(startPlayerPosition.x, startPlayerPosition.y));
        this.createLevers();
        this.isManholeOpen = false;
        this.isGoldDropped = false;
        this.isStart = false;
        this.timer = timer;
    }

    public restart() {}

    createLevers() {
        const angular = this.random.float(0, Math.PI);
        const radius = 300;
        const x = radius * Math.cos(angular);
        const y = radius * Math.sin(angular);
        this.levers[0].position = {x: -x, y: -y};
        this.levers[1].position = {x: x, y: y};

        this.levers[0].trigger = this.physic.lever1;
        Body.setPosition(this.physic.lever1, Vector.create(this.levers[0].position.x, this.levers[0].position.y));
        this.levers[1].trigger = this.physic.lever2;
        Body.setPosition(this.physic.lever2, Vector.create(this.levers[1].position.x, this.levers[1].position.y));

        this.levers[0].state = "off";
        this.levers[1].state = "off";
    }

}

export class LeverModel {
    state: "off" | "hover" | "on" = "off";
    position = {x: 0, y: 0};
    trigger: Body;
}

