interface Point2D {
    x: number;
    y: number;
}

interface Point3D extends Point2D {
    z: number;
}

interface Point extends Point2D {
    z?: number;
}

export function distanceBetweenPoints(a: Point, b: Point) {
    if (a.z !== undefined)
        return distanceBetweenPoint2D(a, b);
    else //@ts-ignore //todo: better checks or type cast ot change definition?
        return distanceBetweenPoint3D(a, b);
}

export function distanceBetweenPoint2D(a: Point2D, b: Point2D) {
    return Math.sqrt(Math.pow(b.x - a.x, 2) + Math.pow(b.y - a.y, 2));
}

export function distanceBetweenPoint3D(a: Point3D, b: Point3D) {
    return Math.sqrt(Math.pow(b.x - a.x, 2) + Math.pow(b.y - a.y, 2) + Math.pow(b.z - a.z, 2));
}

export function findPoint2D(a: Point2D, b: Point2D, lenA: number) {
    let res = {x: b.x - a.x, y: b.y - a.y};
    const k = lenA / distanceBetweenPoint2D(a, b);
    res.x = res.x * k;
    res.y = res.y * k;
    res.x = res.x + a.x;
    res.y = res.y + a.y;
    return res;
}