import {Game} from "@denishussar/html5-game-engine";
import {GameInstance} from "./GameInstance";
import * as WebFont from "webfontloader";
import {SoundConstants} from "./constants/SoundConstants";

declare var require;

//require("../fonts/fonts.css");
const gameCfg = require("../game-config.json");
const version = require("../package.json").version;

export default class main {
    constructor() {
        WebFont.load({
            custom: {
                families: [
                    "Times New Roman",
                    "Arial Black",
                    //"AlligatorManRUS",
                    // "Montserrat",
                    // "Myriad Pro",
                    // "MyriadPro-Regular",
                    // "MyriadPro-Bold",
                ],
            },
            loading: () => {
                this.startGame();
            },
        });

    }

    public startGame(): Game {
        return new Game({
            main: GameInstance,
            version: version,
            sounds: [
                {
                    "id": SoundConstants.COINS,
                    "path": "/sounds/coins-3.mp3",
                },
                {
                    "id": SoundConstants.FIREBALL,
                    "path": "/sounds/fireball-1.mp3",
                },
                {
                    "id": SoundConstants.MANHOLE,
                    "path": "/sounds/hatch-1.mp3",
                },
                {
                    "id": SoundConstants.LEVER,
                    "path": "/sounds/lever-1.mp3",
                },
                {
                    "id": SoundConstants.LIFE_LOSS,
                    "path": "/sounds/life-loss-1.mp3",
                },
                {
                    "id": SoundConstants.MUSIC_MAIN,
                    "path": "/sounds/Music_Main-1.mp3",
                },
                {
                    "id": SoundConstants.MUSIC_ACTION,
                    "path": "/sounds/Music_Action-1.mp3",
                },
                {
                    "id": SoundConstants.RICOCHET,
                    "path": "/sounds/ricochet-1.wav",
                },
                {
                    "id": SoundConstants.MUSIC_INTRO,
                    "path": "/sounds/intro.mp3",
                },
            ],
            assetsRoot: gameCfg.resourcesRoot,
            elementId: "gamePlace",
            noWebGL: false,
            backgroundColor: 0x89C0FC,
            clearBeforeRender: true,
            transparent: false,
            showStats: false,
            disableContextMenu: true,
            antialias: false,
            fps: 60,
            gameId: gameCfg.gameId,
        });
    }

}

new main();
