import {GameRandom, Inject, ModelEvents, Singleton} from "@denishussar/html5-game-engine";
import {Body} from "matter-js";
import {GameConstants} from "../constants/GameConstants";
import playerSpeed = GameConstants.playerSpeed;
import playerHealth = GameConstants.playerHealth;
import playerDamage = GameConstants.playerDamage;

export enum PlayerDirection {
    left,
    right,
}

@Singleton
export class PlayerModel {
    public move: [number, number] = [0, 0];
    public speed = playerSpeed;
    public direction = PlayerDirection.right;
    public collision: Body;

    public health = playerHealth;
    public damage = playerDamage;

    public isImmortal = false;

    public fireballCooldown = 0;
    public dashCooldown = 0;

    public reset() {
        this.move = [0, 0];
        this.direction = PlayerDirection.right;
        this.isImmortal = false;
        this.fireballCooldown = 0;
    }

    constructor(@Inject private _$random: GameRandom,
                @Inject private _$modelEvents: ModelEvents,
    ) {
    }

}

