import {
    AnimationBaseController,
    GameRandom,
    IAnimationControllerConfiguration,
    Inject,
} from "@denishussar/html5-game-engine";
import {GameModel} from "../models/GameModel";
import {GameLogic, GOLD_DROPPED} from "../logic/GameLogic";

export interface IGoldControllerOptions {
}

export class GoldController extends AnimationBaseController {
    $model: GameModel;

    public constructor(@Inject private options: IGoldControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("GoldController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {
        return {
            modelWatch: {
                goldHealth: newValue => this.setTextByQuery("gold_health", newValue.toFixed(0)),
                goldStage: newValue => this.gotoAndStopByQuery("gold", newValue),
            },
            modelEvents: {
                [GOLD_DROPPED]: () => this.fadeOut(),
                "nextLevel": () => this.nextLevel(),
            },
        };
    }

    private interval;
    private timeout;

    protected fadeOut() {
        this.interval = setInterval(() => {
            this.alpha -= .02;
        }, 50);
        this.timeout = setTimeout(() => {
            clearInterval(this.interval);
        }, 5000);
    }

    protected onInitialize(): void {
        this.setTextByQuery("gold_health", this.$model.goldHealth.toFixed(0));
        this.gotoAndStopByQuery("gold", 4);
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
    }

    private nextLevel() {
        clearInterval(this.interval);
        clearTimeout(this.timeout);
        this.alpha = 1;
    }
}

