import {
    AnimationBaseController,
    GameRandom,
    IAnimationControllerConfiguration,
    Inject,
} from "@denishussar/html5-game-engine";
import {GameLogic} from "../logic/GameLogic";
import {Body} from "matter-js";

export interface IProjectileControllerOptions {
}

export class ProjectileController extends AnimationBaseController {
    $model: Body;

    public constructor(@Inject private options: IProjectileControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("ProjectileController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {};
    }

    updatePosition() {
        this.x = this.$model.position.x;
        this.y = this.$model.position.y;
        this.rotation = Math.atan2(this.$model.velocity.x, -this.$model.velocity.y);
        console.log(this.$model.speed);
    }

    protected onInitialize(): void {
        this.framerate = 60;
        this.loop = true;
        this.play();
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
        this.updatePosition();
    }

    protected onResize(width: number, height: number, scale: number): void {
        this.display.scale.set(.5);
    }
}

