export namespace PhysicsConstant {

    //world
    export const thick = 300;
    export const width = 960 + thick;
    export const height = 960 + thick;
    export const padding = 30 + thick / 2;
    export const center = {x: 0, y: 0};

    //misc
    export const fireballRadius = 15;
    export const goldRadius = [10, 20, 40, 60, 90];
    export const leverRadius = 45;

    //player
    export const collisionRadius = 30;
    export const startPlayerPosition = {x: center.x, y: center.y + 2 * height / 6};
    export const dashPower = .1;

    //bound point
    export const topEdge = center.y - height / 2;
    export const rightEdge = center.x + width / 2;
    export const bottomEdge = center.y + height / 2;
    export const leftEdge = center.x - width / 2;

    export const leftTopCorner = {x: leftEdge, y: topEdge};
    export const rightTopCorner = {x: rightEdge, y: topEdge};
    export const leftBottomCorner = {x: leftEdge, y: bottomEdge};
    export const rightBottomCorner = {x: rightEdge, y: bottomEdge};

    //bound point with padding
    export const topEdgeWithPadding = topEdge + padding;
    export const rightEdgeWithPadding = rightEdge - padding;
    export const bottomEdgeWithPadding = bottomEdge - padding;
    export const leftEdgeWithPadding = leftEdge + padding;

    export const leftTopCornerWithPadding = {x: leftEdgeWithPadding, y: topEdgeWithPadding};
    export const rightTopCornerWithPadding = {x: rightEdgeWithPadding, y: topEdgeWithPadding};
    export const leftBottomCornerWithPadding = {x: leftEdgeWithPadding, y: bottomEdgeWithPadding};
    export const rightBottomCornerWithPadding = {x: rightEdgeWithPadding, y: bottomEdgeWithPadding};
}