import {
    AnimationBaseController,
    GameRandom,
    IAnimationControllerConfiguration,
    Inject,
} from "@denishussar/html5-game-engine";
import {GameLogic} from "../../logic/GameLogic";
import {AnimationContents} from "../AnimationContents";
import {PlayerModel} from "../../models/PlayerModel";
import {GameConstants} from "../../constants/GameConstants";
import fireballCooldown = GameConstants.fireballCooldown;

export interface ICharacterControllerOptions {
}

export class CharacterController extends AnimationBaseController {
    $model: PlayerModel;

    public constructor(@Inject private options: ICharacterControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("CharacterController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            animationContent: AnimationContents.LEVEL_CONTENT_JSON,
            libraryName: "magician",
            keyboard: {
                pressed: (keyCode, event) => this.gameLogic.pressKey(keyCode),
                released: (keyCode, event) => this.gameLogic.releaseKey(keyCode),
            },
            modelWatch: {
                direction: () => this.display.scale.x *= -1,
                fireballCooldown: (newValue, oldValue) => this.updateStaff(newValue, oldValue),
            },
        };
    }

    protected onInitialize(): void {
        this.gotoAndStopByQuery("staff", 3);
        for (let i = 0; i < 3; i++) {
            setTimeout(() => this.gotoAndPlayByQuery("star_" + i, 0, true), 200 * i);
        }
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
        const bounds = this.$model.collision.bounds;
        const A = bounds.min;
        const B = bounds.max;
        const width = B.x - A.x;
        const height = B.y - A.y;
        this.x = A.x + width / 2;
        this.y = A.y + height / 2;
    }

    protected onResize(width: number, height: number, scale: number): void {
        this.display.scale.set(.4);
    }

    private updateStaff(newValue, oldValue) {
        if (newValue <= 0 && oldValue > 0) {
            this.gotoAndStopByQuery("staff", 3);
        } else if (newValue < 1000 && oldValue > 1000) {
            this.gotoAndStopByQuery("staff", 2);
        } else if (newValue < 2000 && oldValue > 2000) {
            this.gotoAndStopByQuery("staff", 1);
        } else if (newValue >= fireballCooldown && oldValue < fireballCooldown) {
            this.gotoAndStopByQuery("staff", 0);
        }
    }
}

