import {
    AnimationBaseController,
    GameRandom,
    Inject,
    ISimpleControllerConfiguration,
    SimpleBaseController,
} from "@denishussar/html5-game-engine";
import {GameLogic} from "../../logic/GameLogic";
import {GameModel} from "../../models/GameModel";
import {PhysicsEngine} from "../../PhysicsEngine";
import {WallsController} from "./WallsController";
import {CharacterController} from "./CharacterController";
import {FireballsController} from "../FireballsController";
import {LeversController} from "../LeversController";
import {AnimationContents} from "../AnimationContents";
import {GoldController} from "../GoldController";
import {TopPanelController} from "../TopPanelController";
import {ScoreController} from "../ScoreController";
import {ManholeController} from "../ManholeController";

export interface ILevelRootControllerOptions {
}

const paddingTop = 30;
const paddingLeft = 30;

export class LevelRootController extends SimpleBaseController {
    $model: GameModel;

    public constructor(@Inject private options: ILevelRootControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic,
                       @Inject private physicsFactory: PhysicsEngine) {
        super("LevelRootController");
    }

    protected getConfiguration(): ISimpleControllerConfiguration {

        return {
            onPointerDown: e => this.gameLogic.throwFireball(e.screenPosition),
            childControllers: {
                walls: {
                    controller: WallsController,
                    model: this.$model,
                },
                manhole: {
                    animationContent: AnimationContents.LEVEL_CONTENT_JSON,
                    libraryName: "manhole",
                    controller: ManholeController,
                    model: this.$model,
                },
                levers: {
                    controller: LeversController,
                    model: this.$model,
                },
                gold: {
                    animationContent: AnimationContents.LEVEL_CONTENT_JSON,
                    libraryName: "gold_ui",
                    controller: GoldController,
                    model: this.$model,
                },
                character: {
                    controller: CharacterController,
                    model: this.$model.player,
                },
                fireballsPool: {
                    controller: FireballsController,
                    model: this.$model.physic,
                },
                topPanel: {
                    animationContent: AnimationContents.LEVEL_CONTENT_JSON,
                    libraryName: "ui_top",
                    controller: TopPanelController,
                    model: this.$model,
                },
                bottomPanel: {
                    animationContent: AnimationContents.LEVEL_CONTENT_JSON,
                    libraryName: "ui_bottom",
                    controller: ScoreController,
                    model: this.$model,
                },
            },
        };
    }

    protected onInitialize(): void {}

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
    }

    protected onResize(width: number, height: number, scale: number): void {
        this.$childControllers.get("topPanel").y = -height / scale / 2;
        const topPanel = this.$childControllers.get<AnimationBaseController>("topPanel");
        const bottomPanel = this.$childControllers.get<AnimationBaseController>("bottomPanel");
        topPanel.y = -height / scale / 2 + paddingTop;
        topPanel.setPosXByQuery("life", -width / scale / 2 + paddingLeft);
        topPanel.setPosXByQuery("ui_top_left", width / scale / 2 - paddingLeft);
        bottomPanel.y = height / scale / 2 - paddingTop;
    }
}

