import {Bodies, Body, Vector} from "matter-js";
import {PhysicsConstant} from "../constants/PhysicsConstant";
import worldWidth = PhysicsConstant.width;
import wordlHeight = PhysicsConstant.height;
import padding = PhysicsConstant.padding;
import topEdge = PhysicsConstant.topEdge;
import bottomEdge = PhysicsConstant.bottomEdge;
import leftEdge = PhysicsConstant.leftEdge;
import rightEdge = PhysicsConstant.rightEdge;
import startPlayerPosition = PhysicsConstant.startPlayerPosition;
import playerRadius = PhysicsConstant.collisionRadius;
import goldRadius = PhysicsConstant.goldRadius;
import leverRadius = PhysicsConstant.leverRadius;
import topEdgeWithPadding = PhysicsConstant.topEdgeWithPadding;
import leftEdgeWithPadding = PhysicsConstant.leftEdgeWithPadding;
import rightEdgeWithPadding = PhysicsConstant.rightEdgeWithPadding;
import bottomEdgeWithPadding = PhysicsConstant.bottomEdgeWithPadding;

const stat = () => {return {isStatic: true};};
const rotate45 = () => {return {angle: Math.PI / 3};};
const sides = 3;
const size = 20;

//@Singleton
export class PhysicsModel {

    readonly topWall = Bodies.rectangle(0, topEdge + padding / 2, worldWidth, padding, stat());
    readonly leftWall = Bodies.rectangle(leftEdge + padding / 2, 0, padding, wordlHeight, stat());
    readonly rightWall = Bodies.rectangle(rightEdge - padding / 2, 0, padding, wordlHeight, stat());
    readonly bottomWall = Bodies.rectangle(0, bottomEdge - padding / 2, worldWidth, padding, stat());
    readonly walls = [this.topWall, this.leftWall, this.rightWall, this.bottomWall];

    readonly cornerCircle1 = Bodies.polygon(leftEdgeWithPadding, topEdgeWithPadding, sides, size, stat());
    readonly cornerCircle2 = Bodies.polygon(rightEdgeWithPadding, topEdgeWithPadding, sides, size, stat());
    readonly cornerCircle3 = Bodies.polygon(leftEdgeWithPadding, bottomEdgeWithPadding, sides, size, stat());
    readonly cornerCircle4 = Bodies.polygon(rightEdgeWithPadding, bottomEdgeWithPadding, sides, size, stat());
    readonly cornerCircles = [this.cornerCircle1, this.cornerCircle2, this.cornerCircle3, this.cornerCircle4];

    public reset() {
        Body.setPosition(this.topWall, Vector.create(0, topEdge + padding / 2));
        Body.setPosition(this.leftWall, Vector.create(leftEdge + padding / 2, 0));
        Body.setPosition(this.rightWall, Vector.create(rightEdge - padding / 2, 0));
        Body.setPosition(this.bottomWall, Vector.create(0, bottomEdge - padding / 2));
    }

    readonly lever1 = Bodies.circle(0, 0, leverRadius, {
        isStatic: true,
        isSensor: true,
    });
    readonly lever2 = Bodies.circle(200, 200, leverRadius, {
        isStatic: true,
        isSensor: true,
    });
    readonly levers = [this.lever1, this.lever2];

    readonly gold = Bodies.circle(0, 0, goldRadius[4], stat());

    readonly playerCollision = Bodies.circle(
        startPlayerPosition.x,
        startPlayerPosition.y,
        playerRadius,
        {
            inertia: Infinity,
            frictionAir: .4,
        });

    public fireballs: Body[] = [];

    constructor() {
    }
}

