import {
    AnimationBaseController,
    GameRandom,
    IAnimationControllerConfiguration,
    Inject,
} from "@denishussar/html5-game-engine";
import {GameModel} from "../models/GameModel";
import {GameLogic} from "../logic/GameLogic";
import {LifeController} from "./LifeController";
import {GameConstants} from "../constants/GameConstants";
import timer = GameConstants.timer;

export interface ITopPanelControllerOptions {
}

export class TopPanelController extends AnimationBaseController {
    $model: GameModel;

    public constructor(@Inject private options: ITopPanelControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("TopPanelController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            childControllers: {
                life: {
                    instanceName: "life",
                    controller: LifeController,
                    model: this.$model.player,
                },
            },
            modelWatch: {
                timer: () => this.updateTime(),
            },
        };
    }

    protected onInitialize(): void {
        this.updateTime();
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
    }

    private updateTime() {
        const time = this.$model.timer;
        if (time < 0) return;
        const timeText = (time > 60) ? "1:" + ((time % 60 < 10) ? "0" + time % 60 : time % 60) : time.toFixed(0);
        this.setTextByQuery("ui_top_left.ui_timer.time", timeText);
        this.gotoAndStopByQuery("ui_top_left.ui_timer.progress_bar_timer", Math.floor(100 - time * 99 / timer));
    }
}

