import {
    GameRandom,
    Inject,
    ISimpleControllerConfiguration,
    SimpleBaseController,
} from "@denishussar/html5-game-engine";
import {GameLogic} from "../logic/GameLogic";
import {ProjectileController} from "./ProjectileController";
import {AnimationContents} from "./AnimationContents";
import {PhysicsModel} from "../models/PhysicsModel";
import {Body} from "matter-js";
import {CREATE_FIREBALL_EVENT} from "../PhysicsEngine";

export interface IFireballsPoolControllerOptions {
}

export class FireballsController extends SimpleBaseController {
    $model: PhysicsModel;

    public constructor(@Inject private options: IFireballsPoolControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("FireballsController");
    }

    private ballsCounter = 0;

    protected getConfiguration(): ISimpleControllerConfiguration {
        return {
            modelEvents: {
                [CREATE_FIREBALL_EVENT]: (fireball: Body) => {
                    console.log("fireball");
                    this.addProjectileController(fireball);
                    // const newProjectile = fireballs[0]
                    // if (newProjectile) this.addProjectileController(newProjectile);
                },
                nextLevel: () => {
                    console.log("REMOVE ALL");
                    for (let i = 0; i < this.ballsCounter; i++) {
                        this.removeChildControllerByName("fireball_" + i);
                    }
                    this.ballsCounter = 0;
                },
            },
        };
    }

    private;

    private addProjectileController(last: Body) {
        this.addChildControllerAsync({
            animationContent: AnimationContents.LEVEL_CONTENT_JSON,
            libraryName: "fireball",
            controller: ProjectileController,
            model: last,
        }, false, "fireball_" + (this.ballsCounter++));
    }

    protected onInitialize(): void {

    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {

    }
}

