import {
    FontResourceItem,
    GameRandom,
    Inject,
    ISimpleControllerConfiguration,
    SimpleBaseController,
    SoundType,
} from "@denishussar/html5-game-engine";
import {GameModel} from "./models/GameModel";
import {GameLogic} from "./logic/GameLogic";
import {LevelRootController} from "./controllers/Game/LevelRootController";
import {JoystickController} from "./controllers/Game/JoystickController";
import {SoundConstants, SoundLevels} from "./constants/SoundConstants";

export interface IRootControllerOptions {
    version: string;
}

export class RootController extends SimpleBaseController {
    $model: GameModel;

    public constructor(@Inject private _options: IRootControllerOptions,
                       @Inject private _random: GameRandom,
                       @Inject private _gameLogic: GameLogic) {
        super("RootController");
    }

    protected getConfiguration(): ISimpleControllerConfiguration {

        return {
            childControllers: {
                levelRoot: {
                    controller: LevelRootController,
                    model: this.$model,
                },
                joystick: {
                    controller: JoystickController,
                    model: this.$model,
                },
            },
            resources: {
                font0: new FontResourceItem("AlligatorManRUS"),
            },
            modelWatch: {
                timer: () => this.updateMusic(),
            },
            sounds: [
                {
                    "id": SoundConstants.COINS,
                    "type": SoundType.SFX,
                },
                {
                    "id": SoundConstants.FIREBALL,
                    "type": SoundType.SFX,
                },
                {
                    "id": SoundConstants.MANHOLE,
                    "type": SoundType.SFX,
                },
                {
                    "id": SoundConstants.LEVER,
                    "type": SoundType.SFX,
                },
                {
                    "id": SoundConstants.LIFE_LOSS,
                    "type": SoundType.SFX,
                },
                {
                    "id": SoundConstants.MUSIC_MAIN,
                    "type": SoundType.Music,
                },
                {
                    "id": SoundConstants.MUSIC_ACTION,
                    "type": SoundType.Music,
                },
                {
                    "id": SoundConstants.MUSIC_INTRO,
                    "type": SoundType.Music,
                },
                {
                    "id": SoundConstants.RICOCHET,
                    "type": SoundType.SFX,
                },
            ],
        };
    }

    protected async onInitialize() {
        this.updateMusic();
        alert(`Однажды доблестные маги Зефирного королества обнаружили гробницу древнего Повелителя Лакрицы. Повсюду были предостережения о том, чтобы никто не трогал его сокровища, но жадность магов взяла верх: они унесли все сокровища в свою Волшебную башню.
Спустя неделю в башне началось что-то странное: некоторые маги начинали бормотать на неизвестном языке, а других сразила неизвестная болезнь. Странности становились всё сильнее и сильнее, а некоторых магов даже обуяла агрессия. Все знали, что виной этому были сокровища Повелителя Лакрицы, но только одна Зефирка имела смелость пойти и уничтожить их. Вооружившись волшебной палочкой и странным артефактом с уроков Хрономагии, она начала подниматься к Сокровищнице.
Попав в сокровищницу, Зефирка собрала все свои силы и знания, полученные за 3 урока Колдовства, и метнула Огненный Шар в кучу проклятого золота. Каково же было её удивление, когда пролетев сквозь расплавленное золото, Шар отскочил от защитного барьера Сокровищницы прямо в неё! Воздух пропитался запахом жжёного сахара, а Зефирка поняла, что это конец...
Как вдруг она снова стоит перед невредимой грудой сокровищ, а артефакт, что она умыкнула, начал таинственно светится.
"В этот раз, точно получится!" - подумала Зефирка и снова начала закидывать Огненными шарами злосчастное сокровище.`);
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onResize(width: number, height: number, scale: number): void {
    }

    protected onUpdate(frameRate: number): void {
        this._gameLogic.update(frameRate);
    }

    private updateMusic(): void {
        const isIntro = !this.$model.isStart;
        const isRush = this.$model.timer <= 0;
        const isAction = !isRush && this.$model.isStart;

        let currentMusic;
        if (isIntro) currentMusic = "intro";
        else if (isAction) currentMusic = "action";
        else if (isRush) currentMusic = "rush";

        if (this.$model.currentMusic === currentMusic) return;
        this.$model.currentMusic = currentMusic;

        switch (currentMusic) {
            case "intro":
                this.soundManager.play(SoundConstants.MUSIC_INTRO, true, SoundLevels.musicIntro);
                this.soundManager.stop(SoundConstants.MUSIC_MAIN);
                this.soundManager.stop(SoundConstants.MUSIC_ACTION);
                break;
            case "action":
                this.soundManager.play(SoundConstants.MUSIC_MAIN, true, SoundLevels.musicAction);
                this.soundManager.stop(SoundConstants.MUSIC_INTRO);
                this.soundManager.stop(SoundConstants.MUSIC_ACTION);
                break;
            case "rush":
                this.soundManager.play(SoundConstants.MUSIC_ACTION, true, SoundLevels.musicAction);
                this.soundManager.stop(SoundConstants.MUSIC_MAIN);
                this.soundManager.stop(SoundConstants.MUSIC_INTRO);
                break;
        }
    }
}
