import {GameRandom, Inject, Key, ModelEvents, Singleton, SoundManager} from "@denishussar/html5-game-engine";
import {GameModel} from "../models/GameModel";
import {GameStorageManager} from "@denishussar/html5-game-engine/dist";
import {PlayerDirection} from "../models/PlayerModel";
import {Point} from "pixi.js";
import {PhysicsEngine} from "../PhysicsEngine";
import {Body, Engine, IEventCollision, Vector} from "matter-js";
import {findPoint2D} from "../utils/geometry/distanceBetweenPoints";
import {GameConstants} from "../constants/GameConstants";
import {SoundConstants, SoundLevels} from "../constants/SoundConstants";
import {PhysicsConstant} from "../constants/PhysicsConstant";
import goldMinimalHealth = GameConstants.goldMinimalHealth;
import fireballDamage = GameConstants.fireballDamage;
import playerMinHealth = GameConstants.playerMinHealth;
import goldStage = GameConstants.goldStage;
import fireballCooldown = GameConstants.fireballCooldown;
import wallSpeed = GameConstants.wallSpeed;
import dashCooldown = GameConstants.dashCooldown;
import dashPower = PhysicsConstant.dashPower;

export const GOLD_DROPPED = "GOLD_DROPPED";

@Singleton
export class GameLogic {

    constructor(@Inject private gameModel: GameModel,
                @Inject private random: GameRandom,
                @Inject private modelEvents: ModelEvents,
                @Inject private storageManager: GameStorageManager,
                @Inject private physicsEngine: PhysicsEngine,
                @Inject private soundManager: SoundManager,
    ) {
        this.physicsEngine.subscribe("collisionStart", this.collisionStartHandler.bind(this));
        this.physicsEngine.subscribe("collisionEnd", this.collisionEndHandler.bind(this));
    }

    private collisionStartHandler(event: IEventCollision<Engine>) {
        const bodyA = event.pairs[0].bodyA;
        const bodyB = event.pairs[0].bodyB;

        const isPlayer = bodyA === this.gameModel.player.collision || bodyB === this.gameModel.player.collision;
        const isProjectile = this.gameModel.physic.fireballs.find(ball => ball === bodyA || ball === bodyB);
        const isGold = bodyA === this.gameModel.physic.gold || bodyB === this.gameModel.physic.gold;
        const isLever = this.gameModel.physic.levers.find(lever => lever === bodyA || lever === bodyB);

        if (isPlayer && isProjectile) {
            this.damagePlayer();
        } else if (isGold && isProjectile && !this.gameModel.isGoldDropped) {
            this.damageGold();
        } else if (isPlayer && isLever) {
            this.hoverLever(isLever);
        } else if (isPlayer && isGold && this.gameModel.isManholeOpen) {
            this.goToNextLevel();
        }
    }

    private collisionEndHandler(event: IEventCollision<Engine>) {
        const bodyA = event.pairs[0].bodyA;
        const bodyB = event.pairs[0].bodyB;

        const isPlayer = bodyA === this.gameModel.player.collision || bodyB === this.gameModel.player.collision;
        const isProjectile = this.gameModel.physic.fireballs.find(ball => ball === bodyA || ball === bodyB);
        const isGold = bodyA === this.gameModel.physic.gold || bodyB === this.gameModel.physic.gold;
        const isLever = this.gameModel.physic.levers.find(lever => lever === bodyA || lever === bodyB);
        const isWall = this.gameModel.physic.walls.find(wall => wall === bodyA || wall === bodyB);

        if (isProjectile && isProjectile.speed < 4) {
            const multiplier = 4.03 / isProjectile.speed;
            Body.setVelocity(isProjectile, Vector.create(isProjectile.velocity.x * multiplier, isProjectile.velocity.y * multiplier));
            console.log("SET SPEED");
        }

        if (isPlayer && isProjectile && !this.gameModel.isGoldDropped) {
        } else if (isGold && isProjectile) {
        } else if (isPlayer && isLever) {
            this.unhoverLever(isLever);
        } else if (isPlayer && isGold && this.gameModel.isManholeOpen) {
        } else if (isWall && isProjectile) {
            this.soundManager.playSound(SoundConstants.RICOCHET, 0, SoundLevels.ricochet);
        }
    }

    private damageGold() {
        this.soundManager.playSound(SoundConstants.COINS, 0, SoundLevels.coin);
        this.gameModel.goldHealth -= this.gameModel.player.damage;
        this.gameModel.playerScore += this.gameModel.player.damage;

        if (goldStage[this.gameModel.goldStage] >= this.gameModel.goldHealth)
            this.gameModel.goldStage -= 1;

        if (this.gameModel.goldHealth <= goldMinimalHealth) this.win();
    }

    private damagePlayer() {
        if (this.gameModel.player.isImmortal) return;

        this.setImmortality();

        this.soundManager.playSound(SoundConstants.LIFE_LOSS, 0, SoundLevels.lifeLoss);
        this.gameModel.player.health -= fireballDamage;
        if (this.gameModel.player.health <= playerMinHealth) this.lose();
    }

    private hoverLever(leverBody: Body) {
        this.gameModel.canToggleLever = true;
        const lever = this.gameModel.levers.find(lever => lever.trigger === leverBody);
        lever.state = (lever.state === "off") ? "hover" : "on";
    }

    private unhoverLever(leverBody: Body) {
        this.gameModel.canToggleLever = false;
        const lever = this.gameModel.levers.find(lever => lever.trigger === leverBody);
        lever.state = (lever.state === "hover") ? "off" : "on";
    }

    private goToNextLevel() {
        alert("Отличный раунд! Давай выбьем всю стружку из этого куска металла!");
        this.gameModel.reset();
        this.physicsEngine.reset();
        this.modelEvents.dispatch(this.gameModel, "nextLevel");
        this.modelEvents.dispatch(this.physicsModel, "nextLevel");
    }

    private win() {
        alert("You are win!");
    }

    private lose() {
        alert("Ты поджарен! Отматываем назад...");
        window.location.reload();
    }

    private physicsModel = this.gameModel.physic;

    public pressKey(key: number): void {
        if (true) {
            this.gameModel.wasdState[key] = 1;
            switch (key) {
                //movement
                case Key.D:
                case Key.RIGHT:
                    this.unitMoveStart([1, 0]);
                    break;
                case Key.A:
                case Key.LEFT:
                    this.unitMoveStart([-1, 0]);
                    break;
                case Key.W:
                case Key.UP:
                    this.unitMoveStart([0, -1]);
                    break;
                case Key.S:
                case Key.DOWN:
                    this.unitMoveStart([0, 1]);
                    break;
                //dash
                case Key.SPACE:
                    this.dash();
                    break;
            }
        }
    }

    public releaseKey(key: number): void {
        this.gameModel.wasdState[key] = 0;
        switch (key) {
            //movement
            case Key.D:
            case Key.RIGHT:
                this.unitMoveStop([1, 0]);
                if (this.gameModel.wasdState[Key.A] || this.gameModel.wasdState[Key.LEFT])
                    this.unitMoveStart([-1, 0]);
                break;
            case Key.A:
            case Key.LEFT:
                this.unitMoveStop([-1, 0]);
                if (this.gameModel.wasdState[Key.D] || this.gameModel.wasdState[Key.RIGHT])
                    this.unitMoveStart([1, 0]);
                break;
            case Key.W:
            case Key.UP:
                this.unitMoveStop([0, -1]);
                if (this.gameModel.wasdState[Key.S] || this.gameModel.wasdState[Key.DOWN])
                    this.unitMoveStart([0, -1]);
                break;
            case Key.S:
            case Key.DOWN:
                this.unitMoveStop([0, 1]);
                if (this.gameModel.wasdState[Key.W] || this.gameModel.wasdState[Key.UP])
                    this.unitMoveStart([0, 1]);
                break;

            //lever
            case Key.E:
            case Key.F:
            case Key.ENTER:
                this.toggleLever();
        }
    }

    public toggleLever() {
        const lever = this.gameModel.levers.find(lever => lever.state === "hover");
        if (lever) {
            lever.state = "on";
            this.soundManager.playSound(SoundConstants.LEVER, 0, SoundLevels.lever);
        }
        if (!this.gameModel.levers.find(lever => lever.state != "on")) this.openManhole();
    }

    private openManhole() {
        this.gameModel.isManholeOpen = true;
        this.gameModel.isGoldDropped = true;
        this.modelEvents.dispatch(this.gameModel, GOLD_DROPPED);
        this.soundManager.playSound(SoundConstants.MANHOLE, 0, SoundLevels.manhole);
    }

    public unitMoveStart(direction: [number, number]) {
        const unit = this.gameModel.player;
        if (unit) {
            unit.move = [
                direction[0] || unit.move[0],
                direction[1] || unit.move[1],
            ];
        }
    }

    public unitMoveStop(direction: [number, number]) {
        let unit = this.gameModel.player;
        if (unit) {
            unit.move = [
                direction[0] ? 0 : unit.move[0],
                direction[1] ? 0 : unit.move[1],
            ];
        }
    }

    private count = 0;

    update(frameRate: number) {
        this.count += frameRate;
        this.physicsUpdate(frameRate);
        this.gameModel.fireballCooldown -= frameRate * 1000;
        this.gameModel.dashCooldown -= frameRate * 1000;
        if (this.count > 1) {
            this.count = 0;
            if (this.gameModel.isStart) this.gameModel.timer--;
        }
    }

    physicsUpdate(frameRate: number) {
        const player = this.gameModel.player;
        let moveX: number = player.move[0];
        let moveY: number = player.move[1];
        if (moveX === 0 && moveY === 0) return;

        player.direction = (moveX > 0) ? PlayerDirection.right : PlayerDirection.left;

        const absX: number = Math.abs(moveX);
        const absY: number = Math.abs(moveY);
        if (absX + absY > 1.1) {
            if (absX > 1 / Math.sqrt(2)) moveX /= Math.sqrt(2);
            if (absY > 1 / Math.sqrt(2)) moveY /= Math.sqrt(2);
        } else {

        }

        console.log(moveX, moveY);
        this.physicsModel.playerCollision.force.x += moveX * player.speed;
        this.physicsModel.playerCollision.force.y += moveY * player.speed;

        //сжимаем стены
        if (this.gameModel.timer <= 0) this.wallTrapUpdate();

    }

    private wallTrapUpdate() {
        const topWall = this.physicsModel.topWall;
        Body.setPosition(topWall, Vector.create(topWall.position.x, topWall.position.y + wallSpeed));
        const bottomWall = this.physicsModel.bottomWall;
        Body.setPosition(bottomWall, Vector.create(bottomWall.position.x, bottomWall.position.y - wallSpeed));
        const leftWall = this.physicsModel.leftWall;
        Body.setPosition(leftWall, Vector.create(leftWall.position.x + wallSpeed, leftWall.position.y));
        const rightWall = this.physicsModel.rightWall;
        Body.setPosition(rightWall, Vector.create(rightWall.position.x - wallSpeed, rightWall.position.y));
    }


    throwFireball(pos: Point) {
        if (this.gameModel.fireballCooldown > 0 || Math.abs(pos.x) > 480 || Math.abs(pos.y) > 480) return;
        this.gameModel.fireballCooldown = fireballCooldown;
        this.gameModel.isStart = true;

        this.soundManager.playSound(SoundConstants.FIREBALL, 0, SoundLevels.fireball);

        this.setImmortality();

        this.gameModel.levers.forEach(lever => lever.state = (lever.state === "hover") ? "hover" : "off");
        if (this.gameModel.isManholeOpen) {
            this.soundManager.playSound(SoundConstants.MANHOLE, 0, SoundLevels.manhole);
            this.gameModel.isManholeOpen = false;
        }

        const playerPos = this.physicsModel.playerCollision.position;
        const x = (pos.x - playerPos.x);
        const y = (pos.y - playerPos.y);
        const vec = findPoint2D({x: 0, y: 0}, {x: x, y: y}, .01);
        this.physicsEngine.createFireball(playerPos.x, playerPos.y, Vector.create(vec.x, vec.y));
    }

    private timeoutImmortality;
    private timeoutDash;

    private playerDashCollisionFilter = {
        category: 2,
        mask: 1,
    };
    private playerCollision = this.gameModel.player.collision.collisionFilter;

    private setImmortality(time = 500) {
        this.gameModel.player.isImmortal = true;
        clearTimeout(this.timeoutImmortality);
        this.timeoutImmortality = setTimeout(() => {
            this.gameModel.player.isImmortal = false;
        }, time);
    }


    public dash(): void {
        if (this.gameModel.dashCooldown > 0) return;
        this.gameModel.dashCooldown = dashCooldown;
        clearTimeout(this.timeoutDash);

        const prev = this.gameModel.player.collision.collisionFilter;
        this.gameModel.player.collision.collisionFilter = this.playerDashCollisionFilter;
        this.timeoutDash = setTimeout(() => {
            this.gameModel.player.collision.collisionFilter = this.playerCollision;
        }, 300);

        this.gameModel.player.collision.force.x += this.gameModel.player.collision.velocity.x * dashPower;
        this.gameModel.player.collision.force.y += this.gameModel.player.collision.velocity.y * dashPower;
    }
}
