import {AnimationBaseController, IAnimationControllerConfiguration, Inject} from "@denishussar/html5-game-engine";
import {AnimationContents} from "../AnimationContents";
import {GamePadModel} from "../../models/GamePadModel";
import {GameLogic} from "../../logic/GameLogic";
import {findPoint2D} from "../../utils/geometry/distanceBetweenPoints";

export interface IJoystickControllerOptions {
}

const maxStickRadius = 100;
const padding = 150;

export class JoystickController extends AnimationBaseController {
    $model: GamePadModel;
    private _pointerDown: boolean = false;
    private _pointerOver: boolean = false;
    private _pressed: boolean = false;
    private _enabled: boolean = true;

    public constructor(@Inject private _options: IJoystickControllerOptions,
                       @Inject private _gameLogic: GameLogic) {
        super("JoystickController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            animationContent: AnimationContents.LEVEL_CONTENT_JSON,
            libraryName: "gamepad_joystick",
            childControllers: {},
            modelWatch: {},
            onPointerDown: () => {
                if (!this._enabled) {
                    return;
                }
                this._pointerDown = true;
                //this.gotoAndStop("down");
            },
            onPointerOut: () => {
                if (!this._enabled) {
                    return;
                }
                if (!this._pointerDown) {
                    //this.gotoAndStop(this.selected ? "down" : "up");
                }
                this._pointerOver = false;
            },
            onPointerOver: () => {
                if (!this._enabled) {
                    return;
                }
                if (this._pointerDown) {
                    //this.gotoAndStop("down");
                } else {
                    //this.gotoAndStop(this.selected ? "down" : "over");
                }
                this._pointerOver = true;
                //this.soundManager.play(SoundConstants.BUTTON_ROLLOVER);
            },
            onPointerUp: () => {
                if (!this._enabled) {
                    return;
                }
                this._pointerDown = false;
                this.setPosByQuery("stick", 0, 0);
                this._gameLogic.unitMoveStop([1, 1]);
                if (this._pointerOver) {
                    //this.gotoAndStop(this.selected ? "down" : "over");
                } else {
                    //this.gotoAndStop(this.selected ? "down" : "up");
                }
                //this.soundManager.play(SoundConstants.BUTTON_CLICK);
            },
            onPointerUpOutside: () => {
                if (!this._enabled) {
                    return;
                }
                //this.gotoAndStop(this.selected ? "down" : "up");
                this._pointerDown = false;
                this.setPosByQuery("stick", 0, 0);
                this._gameLogic.unitMoveStop([1, 1]);
            },
            onPointerMove: e => {
                if (!this._pointerDown) {
                    return;
                }

                let x = e.screenPosition.x;
                let y = e.screenPosition.y;
                const length = Math.sqrt(x * x + y * y);
                const n: [number, number] = [x / length, y / length];

                this._gameLogic.unitMoveStart(n);

                if (length > maxStickRadius) {
                    const newPos = findPoint2D({x: 0, y: 0}, {x: x, y: y}, maxStickRadius);
                    x = newPos.x;
                    y = newPos.y;
                }

                this.setPosByQuery("stick", x, y);
            },
        };
    }

    protected onInitialize(): void {

    }

    protected onResize(width: number, height: number, scale: number): void {
        this.display.scale.set(.6);
        this.x = -width / scale / 2 + padding;
        this.y = height / scale / 2 - padding;
    }
}
