import {
    AnimationBaseController,
    GameRandom,
    IAnimationControllerConfiguration,
    Inject,
} from "@denishussar/html5-game-engine";
import {GameModel} from "../../models/GameModel";
import {GameLogic} from "../../logic/GameLogic";
import {AnimationContents} from "../AnimationContents";

export interface IWallsControllerOptions {
}

export class WallsController extends AnimationBaseController {
    $model: GameModel;

    public constructor(@Inject private options: IWallsControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("WallsController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            animationContent: AnimationContents.LEVEL_CONTENT_JSON,
            libraryName: "floor",
            childControllers: {
                // debugTopWall: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.topWall,
                // },
                // debugBottomWall: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.bottomWall,
                // },
                // debugLeftWall: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.leftWall,
                // },
                // debugRightWall: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.rightWall,
                // },
                // debugPlayerCollision: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.playerCollision,
                // },
                // debugTestBall1: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.gold,
                // },
                // debugTestBall2: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.lever1,
                // },
                // debugTestBall3: {
                //     controller: PhysicalBodyController,
                //     model: this.$model.physic.lever2,
                // },
            },
            modelEvents: {
                nextLevel: () => this.display.children[1].scale.set(1),
            },
        };
    }

    protected onInitialize(): void {
        console.log("floor", this.display, this.display.scale);
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    private count = 1;
    private step = .0002;

    protected onUpdate(frameRate: number): void {
        if (this.$model.timer <= 0 && this.count > 0) {
            this.count -= this.step;
            this.display.children[1].scale.set(this.count);
        }
    }
}

