import {
    AnimationBaseController,
    GameRandom,
    IAnimationControllerConfiguration,
    Inject,
} from "@denishussar/html5-game-engine";
import {GameModel} from "../models/GameModel";
import {GameLogic} from "../logic/GameLogic";
import {AnimationContents} from "./AnimationContents";
import {ButtonController} from "./Common/ButtonController";

export interface IScoreControllerOptions {
}

export class ScoreController extends AnimationBaseController {
    $model: GameModel;

    public constructor(@Inject private options: IScoreControllerOptions,
                       @Inject private random: GameRandom,
                       @Inject private gameLogic: GameLogic) {
        super("ScoreController");
    }

    protected getConfiguration(): IAnimationControllerConfiguration {

        return {
            childControllers: {
                btn_teleport: {
                    animationContent: AnimationContents.LEVEL_CONTENT_JSON,
                    libraryName: "btn_teleport",
                    controller: ButtonController,
                    options: {
                        onClick: () => this.gameLogic.dash(),
                    },
                },
                btn_liver: {
                    animationContent: AnimationContents.LEVEL_CONTENT_JSON,
                    libraryName: "btn_liver",
                    controller: ButtonController,
                    options: {
                        onClick: () => this.gameLogic.toggleLever(),
                    },
                },
            },
            modelWatch: {
                playerScore: newValue => this.setTextByQuery("score", newValue.toFixed(0)),
                canToggleLever: () => this.updateLeverButton(),
                dashCooldown: () => this.updateDashButton(),
            },
        };
    }

    private updateLeverButton() {
        this.$childControllers.get<AnimationBaseController>("btn_liver").alpha = (this.$model.canToggleLever) ? 1 : .5;
    }

    private updateDashButton() {
        this.$childControllers.get<AnimationBaseController>("btn_teleport").alpha = (this.$model.dashCooldown > 0) ? .5 : 1;
    }

    protected onInitialize(): void {
        this.setTextByQuery("score", this.$model.playerScore.toFixed(0));
        this.updateLeverButton();
        this.updateDashButton();
    }

    protected onLandscape(): void {
    }

    protected onPortrait(): void {
    }

    protected onUpdate(frameRate: number): void {
    }

    protected onResize(width: number, height: number, scale: number): void {
        const scaleBtn = .6;
        const padding = 25;
        const radius = 328 / 2 * scaleBtn;
        const paddingWithRadius = radius + padding;
        const btnTp = this.$childControllers.get<AnimationBaseController>("btn_teleport");
        const btnToggleLeverthis = this.$childControllers.get<AnimationBaseController>("btn_liver");
        btnTp.display.scale.set(scaleBtn);
        btnToggleLeverthis.display.scale.set(scaleBtn);
        btnTp.x = width / scale / 2 - paddingWithRadius;
        btnTp.y = -paddingWithRadius;
        btnToggleLeverthis.x = width / scale / 2 - paddingWithRadius;
        btnToggleLeverthis.y = -radius * 2 - paddingWithRadius - padding;
    }
}

