import {Bodies, Body, Composite, Engine, Events, IEvent, Runner, Vector} from "matter-js";
import {Inject, ModelEvents, Singleton} from "@denishussar/html5-game-engine";
import {PhysicsConstant} from "./constants/PhysicsConstant";
import {GameModel} from "./models/GameModel";
import fireballRadius = PhysicsConstant.fireballRadius;

@Singleton
export class PhysicsEngine {
    private engine = Engine.create();
    private runner = Runner.create();
    private events = Events;

    private model = this.gameModel.physic;

    constructor(@Inject private gameModel: GameModel,
                @Inject private modelEvents: ModelEvents) {
        this.engine.gravity.x = 0;
        this.engine.gravity.y = 0;
        Composite.add(this.engine.world, [
            ...this.model.walls,
            this.model.playerCollision,
            this.model.gold,
            ...this.model.levers,
            ...this.model.cornerCircles,
        ]);
        Runner.run(this.runner, this.engine);
    }

    public subscribe(event: "collisionStart" | "collisionEnd", callback: (event: IEvent<Body>) => void) {
        Events.on(this.engine, event, callback);
    }

    public reset() {
        // @ts-ignore
        Composite.remove(this.engine.world, [...this.model.fireballs]);
    }

    public createFireball(x: number, y: number, direction: Vector) {
        console.log("createFireball", direction);
        const fireball = Bodies.circle(x, y, fireballRadius, {
            inertia: Infinity,
            force: direction,
            restitution: 1,
            frictionAir: 0,
            friction: 0,
            collisionFilter: {
                category: 2,
                mask: 1,
            },
        });
        console.log("ss");
        this.model.fireballs.unshift(fireball);
        this.modelEvents.dispatch(this.model, CREATE_FIREBALL_EVENT, fireball);
        console.log("before compos");
        Composite.add(this.engine.world, [fireball]);
        console.log("afters compos");
    }
}

export const CREATE_FIREBALL_EVENT = "CreateFireball";